<template>
  <div class="main">
    <crumbs :paths="paths"></crumbs>
    <card>
      <h3 class="main-title">
        新建制度
        <div class="pull-right">
          <button type="button" class="btn btn-primary" @click="saveEdit()">保存</button>
          <button type="button" class="btn btn-default" @click="canceled()">取消</button>
        </div>
      </h3>
      <form class="form-horizontal normal-wrap">
        <div class="form-group">
          <label class="col-sm-2 control-label">标题</label>
          <div class="col-sm-9">
            <input type="text"
                   class="form-control"
                   placeholder="请输入标题"
                   v-model="ruleAdd.title">
          </div>
        </div>
        <div class="form-group">
          <label class="col-sm-2 control-label">正文</label>
          <div class="col-sm-9">
            <textarea cols="50"
                      rows="30"
                      class="form-control"
                      placeholder="请输入正文"
                      v-model="ruleAdd.content">
            </textarea>
          </div>
        </div>
      </form>
    </card>
  </div>
</template>

<script>
import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';
import { Message } from 'element-ui';

import crumbs from '../../component/crumbs.vue';
import card from '../../component/card.vue';

Vue.prototype.$message = Message;

export default {
  name: 'ruleRegulationAdd',
  data() {
    return {
      paths: [
        { name: '规章制度', url: '/rule-regulation', present: false },
        { name: '新建制度', url: `/rule-regulation-add`, present: true }
      ],
      ruleAdd: {
        id: '',
        title: '',
        content: ''
      }
    }
  },
  methods: {
    saveEdit() {
      if (this.ruleAdd.title !== '' && this.ruleAdd.content !== '') {
        axios({
          headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
          method: 'post',
          url: '/service',
          data: qs.stringify({
            data: (() => {
              var obj = {
                command: 'addOrEditRegulations',
                platform: 'web',
                title: this.ruleAdd.title,
                content: this.ruleAdd.content
              };
              return JSON.stringify(obj);
            })()
          })
        }).then((rep) => {
          if (rep.data.statusCode === '10001') {
            this.ruleAdd.id = rep.data.data.id;
            this.$router.push('/rule-regulation');
          } else if (rep.data.statusCode === '10012') {
            window.location.href = 'signIn.html';
          }
        }, (rep) => { });
      } else {
        this.$message({
          message: '请输入标题和正文',
          type: 'warning'
        });
      }
    },
    canceled() {
      this.$router.push('/rule-regulation');
    }
  },
  components: {
    crumbs,
    card
  }
}
</script>

<style lang="sass" scoped>
.normal-wrap {;
  textarea {
    resize: vertical;
  }
}
</style>



